





























import {Component, Vue} from "vue-property-decorator";
import Draggable from "vuedraggable";
import {OMS} from "@/OMS";
import {StandardElement} from "@/template/StandardElement";
import {TemplateElement} from '@/template/TemplateElement';
import {CustomElement} from "@/template/CustomElement";
import {BrowserUtils} from "@/BrowserUtils";
import {CRUD} from "@/api/CRUD";
import {Category, Request} from "@/api/URI";

@Component({
  name: "CustomElementRenderer",
  components: {
    Draggable
  }
})
export default class CustomElementRenderer extends Vue {

  customElements: Array<CustomElement> = [];

  beforeMount() {
    this.getAll();
  }

  async getAll() {
    const crud = new CRUD(Category.CUSTOM_ELEMENT);
    crud.setRequest(Request.GET);
    await crud.shoot({});
    this.customElements = crud.getResult();
  }

  getHEX() {
    return BrowserUtils.randomHEXString();
  }

  hoverEntry(element: CustomElement) {
    OMS.setHoveredElement(element);
  }

  hoverLeave() {
    // @ts-ignore
    OMS.setHoveredElement({});
  }

  cloneElement(): TemplateElement {
    return OMS.getHoveredElement().clone();
  }

  onDrag(element: CustomElement) {
    //
  }

  onDrop() {
    //
  }

}
