














import {Component, Prop, Vue} from "vue-property-decorator";

@Component({
  name: "PropertyAdder",
  components: {}
})
export default class PropertyAdder extends Vue {
  @Prop() value!: never;

  add(newValue: object) {
    const data = {
      // @ts-ignore
      ...this.value,
      ...newValue
    }
    this.$emit('input', data)
  }
}
