











import {Component, Vue} from 'vue-property-decorator';
import {DefaultData} from "@/demo/DefaultData";
import {BrowserUtils} from "@/BrowserUtils";
import {OMS} from "@/OMS";
import BuilderNavbar from "@/components/BuilderUI/navbar/BuilderNavbar.vue";
import StandardElementPanel from "@/components/BuilderUI/panel/StandardElementPanel.vue";
import ElementSmartRenderer from "@/components/BuilderUI/ElementSmartRenderer.vue";
import RightPanel from "@/components/BuilderUI/panel/RightPanel.vue";
import {Template} from "@/template/Template";
import {TemplateStorage} from "@/api/TemplateStorage";
import {TypeParser} from "@/api/TypeParser";

@Component({
  name: "Design",
  components: {BuilderNavbar, RightPanel, ElementSmartRenderer, StandardElementPanel}
})

export default class Design extends Vue {

  get isDebugMode() { return OMS.isDebugMode(); }
  get selectedElement() { return OMS.getSelectedElement() }

  template!: Template
  isLoading = false;

  beforeMount() {
    this.$store.state.template = TemplateStorage.getTemplate();
    OMS.setCustomStyleSource(`/assets/oms/dynamic/customstyle.php/?id=${this.$store.state.template.id}`);

    BrowserUtils.welcome();
    BrowserUtils.setPageTitle(`${ this.$store.getters.template.name } | ตัวสร้างแม่แบบ - SAMT`);

    OMS.getInstance().$store.dispatch("setStandardElement", new DefaultData().initStandardElement());
  }

}
