





























import {Component, Vue} from "vue-property-decorator";
import Draggable from "vuedraggable";
import {OMS} from "@/OMS";
import {StandardElement} from "@/template/StandardElement";
import {TemplateElement} from '@/template/TemplateElement';
import {CustomElement} from "@/template/CustomElement";
import {BrowserUtils} from "@/BrowserUtils";
import {ServerQuery} from "@/api/ServerQuery";
import {Request} from "@/api/URI";

@Component({
  name: "MediaRenderer",
  components: {
    Draggable
  }
})
export default class MediaRenderer extends Vue {

  get medias(): Array<CustomElement> { return OMS.getCustomElement(); }

  getHEX() {
    return BrowserUtils.randomHEXString();
  }

  hoverEntry(element: StandardElement) {
    OMS.setHoveredElement(element);
  }

  hoverLeave() {
    // @ts-ignore
    OMS.setHoveredElement({});
  }

  cloneElement(): TemplateElement {
    return OMS.getHoveredElement().clone();
  }

  onDrag(element: StandardElement) {
    //
  }

  onDrop() {
    //
  }

}
