
















































import {Component, Prop, Vue} from "vue-property-decorator";
import {AreaTag} from "@/template/area/AreaTag";

@Component({
  name: "PropertyTag",
  components: {}
})
export default class PropertyTag extends Vue {
  @Prop({ required: true }) label!: string;
  @Prop({ required: true }) value!: Array<AreaTag>;
  @Prop() type!: string;

  @Prop() editor = false;

  isAreaEditorActive = false;

  onClickEdit() {
    this.isAreaEditorActive = true;
  }

  remove(index: number) {
    this.value.splice(index, 1);
  }

}
