



























import {Component, Vue} from "vue-property-decorator";
import Draggable from "vuedraggable";
import {OMS} from "@/OMS";
import {StandardElement} from "@/template/StandardElement";
import {TemplateElement} from '@/template/TemplateElement';

@Component({
  name: "StandardElementPanel",
  components: {
    Draggable
  }
})
export default class StandardElementPanel extends Vue {

  get standardElements(): Array<StandardElement> { return OMS.getStandardElement(); }

  hoverEntry(element: StandardElement) {
    OMS.setHoveredElement(element);
  }

  hoverLeave() {
    // @ts-ignore
    OMS.setHoveredElement({});
  }

  cloneElement(): TemplateElement {
    return OMS.getHoveredElement().clone();
  }

  onDrag(element: StandardElement) {
    //
  }

  onDrop() {
    console.log("onDrop");
    OMS.pendingSave();
  }

}
