










import {Component, Prop, Vue} from "vue-property-decorator";
import {TemplateElement} from "@/template/TemplateElement";
import LayerContentChild from "@/components/BuilderUI/panel/rightpanel/LayerContentChild.vue";

@Component({
  name: "LayerContent",
  components: {LayerContentChild}
})
export default class LayerContent extends Vue {
  @Prop() element!: TemplateElement;

  onDelete(index: number) {
    this.element.children.splice(index, 1);
  }
}
