



































import {Component, Prop, Vue, Watch} from "vue-property-decorator";
import Draggable from "vuedraggable";
import {TemplateElement} from "@/template/TemplateElement";
import {OMS} from "@/OMS";
import {AreaTag} from "@/template/area/AreaTag";
import {Category, Request} from "@/api/URI";
import {CRUD} from "@/api/CRUD";

@Component({
  name: "ElementSmartRenderer",
  components: {
    Draggable
  },
})

export default class ElementSmartRenderer extends Vue {
  @Prop() element!: TemplateElement;
  @Prop() order!: number;

  showMenu = false;
  isDrag = false;

  get isDebugMode(): boolean {
    return OMS.isDebugMode()
  }

  get dummyElement() {
    if(this.element.tag != "iframe") return this.element;

    const newElement: TemplateElement = new TemplateElement("div", {class: "title is-4"}, `สื่อจำลอง`, [], []);
    return newElement;
  }

  getComponentData(prop: { class: string, style: string, href: string }) {
    prop = {
      ...prop,
      class: `${prop.class} render-element`,
    }
    return {on: {}, attrs: prop};
  }

  created() {
    console.log(`%c[Child] Rendered: ${this.element.childTags || 'error'} == ${this.element.isLock}`, 'color: #bada55');
  }

  @Watch("element")
  onSave() {
    console.log(`${this.element.getSign} has a change...!`);
  }

  onSaveAsCustom() {
    console.log(`Save ${this.element.getSign} as CustomElement`);
  }

  onHover(element: TemplateElement) {
    if (this.isDebugMode) return 'border: 2px solid black; margin: 8px; transition: 0s;';
    if (this.element.canPutParent(element)) return 'background-color: #EAFFE0; transition: 0.5s;';
    if (this.showMenu) return 'transition: 0.5s;';
    return "cursor: copy";
  }

  onClick(): void {
    console.log(this.element)
  }

  hoverEntry(): void {
    this.showMenu = true;
  }

  hoverLeave(): void {
    this.showMenu = false;
  }

  onDrag(): void {
    console.log("onDrag");
    this.isDrag = true;
    OMS.setHoveredElement(this.element);
  }

  onDrop() {
    console.log("onDrop");
    this.isDrag = false;
    OMS.pendingSave();
  }

  onChoose(childElement: TemplateElement) {
    OMS.setHoveredElement(this.element);
    console.log("onChoose")
    console.log(this.element);
    this.showMenu = true;
  }

  onEdit(childElement: TemplateElement) {
    OMS.setSelectedElement(childElement);
  }

  onDeleteChild(index: number) {
    this.element.children.splice(index, 1);
  }

  get showTool() {
    return this.element.typeTags.includes(this.$store.getters.selectMode);
  }

  get showPlaceMessage() {
    if (this.element.childTags.includes(AreaTag.NO_MORE_CHILD)) return false;
    return this.element.children.length === 0;
  }

  isLastChild() {
    return this.element.children.length == 0;
  }

  log(value: any) {
    console.log(value);
  }

}
