






import {Component, Prop, Vue} from 'vue-property-decorator';
import PreviewElementSmartRenderer from "@/components/BuilderUI/PreviewElementSmartRenderer.vue";
import PreviewBox from "@/components/BuilderUI/PreviewBox.vue";
import {CRUD} from "@/api/CRUD";
import {Category, Request} from "@/api/URI";
import {Auth} from "@/api/auth/Auth";
import {OMS} from "@/OMS";

@Component({
  components: {PreviewBox, PreviewElementSmartRenderer},
})
export default class SaveAsCustomModal extends Vue {

  @Prop() isActive = false;
  name = `องค์ประกอบ`;
  description = "";

  get selectedElement() { return OMS.getSelectedElement(); }
  get user() { return Auth.getUser(); }

  async onSave() {
    const element = this.selectedElement;
    const crud = new CRUD(Category.CUSTOM_ELEMENT);
    crud.setRequest(Request.CREATE);
    crud.setAttachData(element);
    await crud.shoot({name: this.name, description: this.description, owner: Auth.getUser()});

    this.isActive = false;
  }

}
