




















import {Component, Prop, Vue} from "vue-property-decorator";
import Draggable from "vuedraggable";
import {TemplateElement} from "@/template/TemplateElement";
import {OMS} from "@/OMS";
import {AreaTag} from "@/template/area/AreaTag";

@Component({
  name: "LayerContentChild",
  components: {
    Draggable
  }
})
export default class LayerContentChild extends Vue {
  @Prop() element!: TemplateElement;
  isActive = false;

  get isDebugMode(): boolean {
    return OMS.isDebugMode();
  }

  get isCanPut(): boolean {
    return !("children" in this.element);
  }

  getComponentData(prop: object) {
    return {
      on: {},
      attrs: prop
    };
  }

  created() {
    console.log(`%c[Child] Rendered: ${this.element.childTags || 'error'}`, 'color: #bada55');
  }

  click(): void {
    console.log(this.element)
  }

  onDrag(): void {
    OMS.setHoveredElement(this.element);
  }

  onDrop() {
    console.log("onDrop");
  }

  onEdit() {
    OMS.setSelectedElement(this.element);
  }

  onDelete(index: number) {
    this.element.children.splice(index, 1);
  }

}
