
















































import {Component, Prop, Vue} from "vue-property-decorator";
import {OMS} from "@/OMS";

@Component({
  name: "PropertyInput",
  components: {}
})
export default class PropertyInput extends Vue {
  @Prop({ required: true }) label!: string;
  @Prop({ required: true }) value!: never;
  @Prop() type!: string;

  @Prop() selectData!: Array<string>;

  @Prop() placeholder!: boolean;

  placeholderMode = false;

  get loadedTemplate() {
    return OMS.getTemplate();
  }

}
