import {Component, Vue} from "vue-property-decorator";
import Draggable from "vuedraggable";
import {OMS} from "@/OMS";
import {TemplateElement} from "@/template/TemplateElement";
import {LocateManager} from "@/LocateManager";

import RightPanelGroup from "@/components/BuilderUI/panel/rightpanel/RightPanelGroup.vue";
import RightPanelContent from "@/components/BuilderUI/panel/rightpanel/RightPanelContent.vue";
import PropertyInputGroup from "@/components/BuilderUI/panel/rightpanel/PropertyInputGroup.vue";
import PropertyInput from "@/components/BuilderUI/panel/rightpanel/PropertyInput.vue";
import PropertyAdder from "@/components/BuilderUI/panel/rightpanel/PropertyAdder.vue";
import PropertyTag from "@/components/BuilderUI/panel/rightpanel/PropertyTag.vue";
import LayerContent from "@/components/BuilderUI/panel/rightpanel/LayerContent.vue";
import CustomElementRenderer from "@/components/BuilderUI/panel/rightpanel/CustomElementRenderer.vue";
import MediaRenderer from "@/components/BuilderUI/panel/rightpanel/MediaRenderer.vue";
import {CRUD} from "@/api/CRUD";
import {Category, Request} from "@/api/URI";
import {Auth} from "@/api/auth/Auth";
import SaveAsCustomModal from "@/components/BuilderUI/modal/SaveAsCustomModal.vue";
import {Media, MediaType} from "@/template/Media";
import {AreaTag} from "@/template/area/AreaTag";
import PreviewElementSmartRenderer from "@/components/BuilderUI/PreviewElementSmartRenderer.vue";
import {TypeTag} from "@/template/area/TypeTag";

@Component({
  name: "RightPanel",
  components: {
    PreviewElementSmartRenderer,
    Draggable,
    SaveAsCustomModal,
    MediaRenderer,
    CustomElementRenderer,
    PropertyTag,
    PropertyAdder,
    LayerContent,
    PropertyInput,
    PropertyInputGroup,
    RightPanelContent,
    RightPanelGroup
  }
})
export default class RightPanel extends Vue {

  get selectedElement() { return OMS.getSelectedElement(); }
  get loadedTemplate() { return OMS.getTemplate(); }
  get user() { return Auth.getUser(); }

  isSaveModal = false;

  mediaBuilderConfig = {
    displayType: null,
    isRounded: false
  }

  toBePush = {
    button: {
      text: "",
      src: "",
      type: ""
    },
    form: {
      label: "",
      inputName: "",
      placeholder: "",
      inputType: "",
      type: "",
      inputValue: ""
    }
  };

  selectedMedia: Media = new Media(0, "", "", MediaType.IMAGE, "", "");
  mediaList: Array<Media> = [];

  saveAsCustom = {
    name: `องค์ประกอบที่กำหนดเองใหม่`,
    description: ``,
  }

  beforeMount() {
    this.$store.dispatch("updateSelectedElement", {});
  }

  mounted() {
    this.getAllMedia();
  }

  hoverEntry() {
    console.log("HOVERED MEDIA");
    OMS.setHoveredElement(this.virtualMediaElement);
    console.log(OMS.getHoveredElement());
  }

  hoverLeave() {
    // @ts-ignore
    OMS.setHoveredElement({});
  }

  addButton() {
    this.selectedElement.addChild(
        new TemplateElement(
            'a',
            { class: `button ${this.toBePush.button.type}`, src: `${this.toBePush.button.src || '#'}` },
            this.toBePush.button.text,
            [],
            [AreaTag.NO_MORE_CHILD]
        ).setLock(true).clone()
    )

    this.toBePush.button.src = "";
    this.toBePush.button.text = "";
  }

  addFormChild() {
    this.selectedElement.addChild(
        new TemplateElement(
            "div",
            {class: "field"},
            "",
            [],
            []
        ).setLock(true).setLock(true).setChildren([
          new TemplateElement(
              "label",
              {class: "label"},
              this.toBePush.form.label,
              [],
              [AreaTag.NO_MORE_CHILD]),
          new TemplateElement(
              "div",
              {class: "control"},
              "",
              [],
              [])
              .setLock(true).setChildren([
                new TemplateElement(
                    "input",
                    {
                      class: `input ${this.toBePush.form.type}` ,
                      type: "text",
                      placeholder: this.toBePush.form.placeholder,
                      inputValue: this.toBePush.form.inputValue,
                      name: this.toBePush.form.inputName
                    },
                    "",
                    [],
                    [AreaTag.NO_MORE_CHILD]).setLock(true),
              ])
        ]).clone()
    )

    this.toBePush.form.label = "";
    this.toBePush.form.type = "";
    this.toBePush.form.placeholder = "";
    this.toBePush.form.inputValue = "";
  }

  get virtualMediaElement(): TemplateElement {
    return new TemplateElement(
        "figure",
        { class: `image ${this.mediaBuilderConfig.displayType}` },
        "",
        [AreaTag.TYPOGRAPHY],
        [])
        .setLock(true)
        .setTypeTags([TypeTag.MEDIA])
        .setChildren([
          new TemplateElement(
              "img", {
                class: this.mediaBuilderConfig.isRounded ? "is-rounded" : "",
                src: this.selectedMedia.directLink
              },
              "",
              [],
              [])
              .setLock(true)
              .setTypeTags([TypeTag.MEDIA])
        ]);
  }

  cloneElement(): TemplateElement {
    return OMS.getHoveredElement().clone();
  }

  async getAllMedia() {
    const crud = new CRUD(Category.MEDIA);
    crud.setRequest(Request.GET);
    await crud.shoot({});
    this.mediaList = crud.getResult();
  }

  async onSave() {
    const element = this.selectedElement;
    const crud = new CRUD(Category.CUSTOM_ELEMENT);
    crud.setRequest(Request.CREATE);
    crud.setAttachData(element);
    await crud.shoot({name: this.saveAsCustom.name, description: this.saveAsCustom.description, owner: Auth.getUser()});

    this.isSaveModal = false;
  }

  get body(): Array<TemplateElement> {
    return OMS.getInstance().$store.state.template.body;
  }

  getLocate(key: string) {
    return LocateManager.getLocate(key);
  }

  refreshTemplate() {
    OMS.setCustomStyleSource("");
    OMS.saveTemplate().then(() => {
      OMS.setCustomStyleSource(`/assets/oms/dynamic/customstyle.php/?id=${this.$store.getters.template.id}`);
    });
  }

  onSelect(child: TemplateElement) {
    this.$store.dispatch("updateSelectedElement", child);
  }

  onDeleteChild(index: number) {
    this.$store.state.selectedElement.children.splice(index, 1);
  }

  onDelete(index: number) {
    this.body.splice(index, 1);
  }

  toggleLock() {
    this.selectedElement.isLock = !this.selectedElement.isLock;
    this.$buefy.toast.open({
      duration: 1500,
      message: this.selectedElement.isLock ? `<span><i class="fas fa-lock"></i> ล็อคไม่ให้องค์ประกอบนี้ขยับได้แล้ว</span>`: `<span><i class="fas fa-unlock"></i> ปลดล็อคไม่ให้องค์ประกอบนี้ขยับได้แล้ว</span>`,
      position: 'is-top-right',
      type: this.selectedElement.isLock ? `is-danger`: `is-success`,
      queue: false
    })
  }

  get editClassStyle() {
    return  {
      wrapper: 'field',
      input: 'title is-5',
      buttons: 'buttons',
      buttonOk: 'button is-success is-small',
      buttonCancel: 'button is-light is-small',
    }
  }

}
